import React, { useEffect, useState } from "react";
import "./Card.css";
import Chart from "react-apexcharts";
import LinearWithValueLabel from "./Progressbar";
import { Stack } from "@mui/material";
import { COLORS } from "../../helpers/constants";
import RefreshIcon from "../../assets/svg/refresh.svg"
import polygonIcon from "../../assets/svg/polygon.svg"


const CardComponent = ({ data, expanded, onToggle,clickMore }) => {
  
  const total = 100;
  return (
    <div
      className={`card-container ${expanded ? "expanded" : ""} ${
        data.status === "Running" ? "running" : ""
      } ${data.status === "Maintenance" ? "maintenance" : ""} ${
        data.status === "Disconnected" ? "disconnected" : ""
      }`}
      style={{
        zIndex: expanded ? 10 : 1,
        position: expanded ? "absolute" : "relative",
        width: "96%",
        
      }}
    >
      <div className="refresh-icon">
      <img  src={RefreshIcon}/>
        {/* <svg
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M12.0791 2.25001C7.28511 2.25001 3.34511 5.91301 2.96111 10.583H2.00011C1.85142 10.5829 1.70607 10.6271 1.58251 10.7098C1.45896 10.7925 1.36278 10.9101 1.30619 11.0476C1.24961 11.1851 1.23517 11.3363 1.26471 11.482C1.29425 11.6277 1.36644 11.7614 1.47211 11.866L3.15211 13.532C3.29257 13.6712 3.48234 13.7494 3.68011 13.7494C3.87788 13.7494 4.06765 13.6712 4.20811 13.532L5.88811 11.866C5.99378 11.7614 6.06597 11.6277 6.09551 11.482C6.12505 11.3363 6.11061 11.1851 6.05403 11.0476C5.99744 10.9101 5.90126 10.7925 5.77771 10.7098C5.65415 10.6271 5.5088 10.5829 5.36011 10.583H4.46711C4.84711 6.75201 8.10511 3.75001 12.0791 3.75001C13.3893 3.74748 14.6783 4.08107 15.8228 4.71888C16.9673 5.35669 17.929 6.27741 18.6161 7.39301C18.6664 7.47958 18.7335 7.55521 18.8134 7.61544C18.8934 7.67566 18.9846 7.71928 19.0817 7.7437C19.1788 7.76812 19.2798 7.77286 19.3787 7.75764C19.4777 7.74242 19.5725 7.70755 19.6578 7.65507C19.7431 7.6026 19.8169 7.53359 19.8751 7.45211C19.9333 7.37063 19.9745 7.27834 19.9965 7.18066C20.0184 7.08298 20.0205 6.9819 20.0028 6.88338C19.985 6.78486 19.9477 6.69089 19.8931 6.60701C19.0719 5.27342 17.9225 4.17267 16.5547 3.40988C15.1868 2.64709 13.6453 2.24775 12.0791 2.25001Z"
            fill="black"
          />
          <path
            opacity="0.5"
            d="M20.8409 10.467C20.7006 10.3283 20.5112 10.2506 20.3139 10.2506C20.1166 10.2506 19.9273 10.3283 19.7869 10.467L18.0999 12.133C17.994 12.2375 17.9215 12.3711 17.8917 12.5169C17.862 12.6627 17.8762 12.8141 17.9327 12.9517C17.9892 13.0894 18.0854 13.2072 18.209 13.29C18.3327 13.3728 18.4781 13.417 18.6269 13.417H19.5259C19.1449 17.247 15.8749 20.25 11.8819 20.25C10.5676 20.2534 9.27432 19.9203 8.12517 19.2824C6.97601 18.6446 6.00925 17.7232 5.31694 16.606C5.26622 16.5201 5.19888 16.4452 5.11886 16.3856C5.03884 16.3261 4.94775 16.2831 4.85092 16.2592C4.75409 16.2353 4.65346 16.231 4.55492 16.2464C4.45639 16.2619 4.36193 16.2968 4.27707 16.3492C4.1922 16.4016 4.11865 16.4704 4.0607 16.5516C4.00276 16.6328 3.96158 16.7247 3.93959 16.822C3.91761 16.9193 3.91524 17.02 3.93264 17.1182C3.95005 17.2164 3.98686 17.3102 4.04094 17.394C4.86751 18.7286 6.02187 19.8296 7.39417 20.592C8.76647 21.3545 10.311 21.7531 11.8809 21.75C16.6899 21.75 20.6469 18.09 21.0319 13.417H21.9999C22.1487 13.417 22.2942 13.3728 22.4178 13.29C22.5415 13.2072 22.6376 13.0894 22.6941 12.9517C22.7506 12.8141 22.7649 12.6627 22.7351 12.5169C22.7054 12.3711 22.6329 12.2375 22.5269 12.133L20.8409 10.467Z"
            fill="black"
          />
        </svg> */}
      </div>

      <div className="heading">
        <h1>{data.name}</h1>
        <p>Status |<span className="status-text">{data.status}</span></p>
      </div>

      <div className="pie" pointer>
        <Chart
          type="donut"
          width="100%"
          height="100%"
          series={[data.completed, total - data.completed]}
          options={{
            chart: { id: "donut-chart" },
            colors: [
              `${data.completed > 50 ? "#27AE60" : "#FF0000"}`,
              "#ffffff",
            ],
            labels: ["Used", "Remaining"],
            plotOptions: {
              pie: {
                startAngle: -90,
                endAngle: 90,
                donut: { size: "65%" },
                expandOnClick: false,
              },
            },
            stroke: {
              colors: ["#D9D9D9"],
            },
            legend: { show: false },
            dataLabels: { enabled: false },
            tooltip: { enabled: false },
          }}
        />
        <div className={`chart-percentage`}>{`${
          data.completed > 0 ? `${data.completed}%` : ""
        }`}</div>
      </div>

      <Stack sx={{px:2,backgroundColor:COLORS.RADIAL_LG_GRAY,mt:-7,mb:1}}>
      <div className="bar-chart-container">
        <div className="bar-label">Availability</div>
        <LinearWithValueLabel
          type="Availability"
          progress={
            data.machineStats.availability > 0
              ? data.machineStats.availability
              : 0
          }
        />
        <div>Performance</div>
        <LinearWithValueLabel
          type="Performance"
          progress={
            data.machineStats.performance > 0
              ? data.machineStats.performance
              : 0
          }
        />
        <div>Quality</div>
        <LinearWithValueLabel
          type="Quality"
          progress={
            data.machineStats.quality > 0 ? data.machineStats.quality : 0
          }
        />
      </div>
      </Stack>
      {expanded && (
        <div className="additional-info">
          <div className="product-metrics-section">
            <h3>Product Metrics</h3>
            <div className="metrics">
              <div className="metric-row">
                <span className="metric-label">Total</span>
                <span className="metric-value">
                  {data.expanded.productMetrics.total}
                </span>
              </div>
              <div className="metric-row">
                <span className="metric-label">Good</span>
                <span className="metric-value">
                  {data.expanded.productMetrics.good}
                </span>
              </div>
              <div className="metric-row">
                <span className="metric-label">NG</span>
                <span className="metric-value">
                  {data.expanded.productMetrics.ng}
                </span>
              </div>
              <div className="metric-row">
                <span className="metric-label">Pass</span>
                <span className="metric-value">
                  {data.expanded.productMetrics.pass}
                </span>
              </div>
              <div className="metric-row">
                <span className="metric-label">Fail</span>
                <span className="metric-value">
                  {data.expanded.productMetrics.fail}
                </span>
              </div>
            </div>
          </div>
          <div className="cycle-info-section">
            <h3>Cycle per Second</h3>
            <div className="metrics">
              <div className="metric-row">
                <span className="metric-label">Best</span>
                <span className="metric-value">
                  {data.expanded.cyclePerSecond.best}
                </span>
              </div>
              <div className="metric-row">
                <span className="metric-label">Worst</span>
                <span className="metric-value">
                  {data.expanded.cyclePerSecond.average}
                </span>
              </div>
              <div className="metric-row">
                <span className="metric-label">Average</span>
                <span className="metric-value">
                  {data.expanded.cyclePerSecond.worst}
                </span>
              </div>
            </div>
            {clickMore &&(
            <div className="more-button">
              <button onClick={()=>clickMore()}>More</button>
            </div>
            )}
          </div>
        </div>
      )}
      <div
        className={`drop-icon ${expanded ? "rotated" : ""}`}
        onClick={onToggle}
      >
        <img  src={polygonIcon}/>
        {/* <svg
          width="26"
          // height="38"
          // top="25"
          
          viewBox="0 0 24 15"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path d="M12 15L0.741669 0.75L23.2583 0.75L12 15Z" fill="#D9D9D9" />
        </svg> */}
      </div>
    </div>
  );
};

export default CardComponent;