import React, { useState } from 'react';
import { Link } from "react-router-dom";
import {
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
} from "@mui/material";
import "./ReportIssue.css";
import DetailsCard from './DetailsCard/DetailsCard';

const parts = [
  { id: 1, name: "Operator 1", code: "Machine 1", qty: "12:00 PM", vendor: "Cutting", issue: "" },
  { id: 2, name: "Operator 2", code: "Machine 2", qty: "01:30 PM", vendor: "Welding", issue: "" },
  { id: 3, name: "Operator 3", code: "Machine 3", qty: "02:00 PM", vendor: "Assembly", issue: "" },
  { id: 4, name: "Operator 3", code: "Machine 3", qty: "02:00 PM", vendor: "Assembly", issue: "" },
  { id: 5, name: "Operator 3", code: "Machine 3", qty: "02:00 PM", vendor: "Assembly", issue: "" },


];

const PopupCard = ({ part, onClose }) => {
  const [selectedQuestions, setSelectedQuestions] = useState([]);
  const [comment, setComment] = useState("");

  const questions = [
    { id: 1, question: "          ?" },
    { id: 2, question: "Wiring Harness  Function   ?" },
    { id: 3, question: "TBO  Full Form   ?" },
    { id: 4, question: "Black-Blue     ?" },
    { id: 5, question: "Blue     ?" },
    { id: 6, question: "Blue     ?" },
    
  ];

  const handleCheckboxChange = (questionId) => {
    setSelectedQuestions((prev) =>
      prev.includes(questionId) ? prev.filter((id) => id !== questionId) : [...prev, questionId]
    );
  };

  const handleCommentChange = (event) => {
    setComment(event.target.value);
  };

  const handleSubmit = () => {
    const selectedQuestionTexts = questions
      .filter((q) => selectedQuestions.includes(q.id))
      .map((q) => q.question);

    console.log("Selected Questions:", selectedQuestionTexts);
    console.log("Comment:", comment);

    onClose();
  };

  return (
    <div className="popup-overlay">
      <div className="popup-card">
        <div className="popup-header">
          <h2>Report Issue</h2>
          <div className="close-icon" onClick={onClose}>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="64"
              height="44"
              fill="none"
              viewBox="0 0 48 48"
            >
              <path
                fill="#F84343"
                d="M24 4C12.94 4 4 12.94 4 24s8.94 20 20 20 20-8.94 20-20S35.06 4 24 4m0 36c-8.82 0-16-7.18-16-16S15.18 8 24 8s16 7.18 16 16-7.18 16-16 16m7.18-26L24 21.18 16.82 14 14 16.82 21.18 24 14 31.18 16.82 34 24 26.82 31.18 34 34 31.18 26.82 24 34 16.82z"
              />
            </svg>
          </div>
        </div>

        <table className="table-container">
          <tbody>
            {questions.map((question) => (
              <tr key={question.id} className="table-row">
                <td className="checkbox-cell">
                  <input
                    type="checkbox"
                    className="checkbox"
                    checked={selectedQuestions.includes(question.id)}
                    onChange={() => handleCheckboxChange(question.id)}
                  />
                </td>
                <td className="question-cell">{question.question}</td>
              </tr>
            ))}
          </tbody>
        </table>

        <div className="comment-section">
          <input
            type="text"
            placeholder="Enter your Comments"
            value={comment}
            onChange={handleCommentChange}
          />
        </div>

        <div style={{ display: 'flex', justifyContent: 'center', marginTop: '20px' }}>
  <button
    style={{
      height: '50px',
      backgroundColor: 'rgb(134, 213, 106)',
      borderRadius: '4px',
      border: 'none',
      fontSize: '24px',
      width: '120px',
      color:'#3E3E3E'
    }}
    className="submit-button"
    type="button"
    onClick={handleSubmit}
  >
    Submit
  </button>
</div>
      </div>
    </div>
  );
};

function ReportOperatorTable() {
  const [isPopupOpen, setIsPopupOpen] = useState(false);
  const [popupData, setPopupData] = useState(null);

  const handleReportClick = (part) => {
    setPopupData(part);
    setIsPopupOpen(true);
  };

  const handleClosePopup = () => {
    setIsPopupOpen(false);
    setPopupData(null);
  };

  return (
    <TableContainer sx={{ }}>
      <Table sx={{ borderSpacing: "0 5px", borderCollapse: "separate" }} aria-label="operator table">
        <TableHead>
          <TableRow sx={{ backgroundColor: "#D9D9D9" }}>
            <TableCell sx={{ fontSize: "14px" ,fontWeight:'600'}}>S. No.</TableCell>
            <TableCell sx={{ fontSize: "14px" ,fontWeight:'600'}}>Operator</TableCell>
            <TableCell sx={{ fontSize: "14px" ,fontWeight:'600'}}>Machine</TableCell>
            <TableCell sx={{ fontSize: "14px" ,fontWeight:'600'}}>Finish Time</TableCell>
            <TableCell sx={{ fontSize: "14px" ,fontWeight:'600'}}>Process</TableCell>
            <TableCell sx={{ fontSize: "14px" ,fontWeight:'600'}}>Issue</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {parts.map((part) => (
            <TableRow key={part.id} sx={{ backgroundColor: "#EEEEEE" }}>
              <TableCell sx={{ fontSize: "14px" }}>{part.id}</TableCell>
              <TableCell sx={{ fontSize: "14px" }}>{part.name}</TableCell>
              <TableCell sx={{ fontSize: "14px" }}>{part.code}</TableCell>
              <TableCell sx={{ fontSize: "14px" }}>{part.qty}</TableCell>
              <TableCell sx={{ fontSize: "14px" }}>{part.vendor}</TableCell>
              <TableCell sx={{ fontSize: "14px" }}>
                <Link
                  to="/"
                  onClick={(e) => {
                    e.preventDefault();
                    handleReportClick(part);
                  }}
                  style={{ color: "#1535DB", textDecoration: "none" }}
                >
                  Report Issue
                </Link>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      {isPopupOpen && <PopupCard part={popupData} onClose={handleClosePopup} />}
    </TableContainer>
  );
}

const ReportIssue = () => {
  return (
    <div className='mx-3 my-2 '>
      <DetailsCard />
      <div className='report-issue-table'>
        <ReportOperatorTable />
        </div>
      
    </div>
  );
};

export default ReportIssue;
