import "./style.scss";

import { useNavigate, useSearchParams } from "react-router-dom";
import React, { useState, useEffect, useRef } from "react";
import Table from "../../../components/tablee";

// components
import Button from "../../../components/buttonn";
import Breadcrumb from "../../../components/breadcrumb";
import CheckCircleOutlineIcon from "@mui/icons-material/CheckCircleOutline";
import Modal from "../../../components/modal";

// Assets
import crossIcon from "../../../assets/svg/cross-icon.svg";
// constants
import { COLLECTION_STATUS } from "../../../helpers/constants";

// immediate components
import LabelsGeneratedModal from "./LabelsGeneratedModal";
import KittingQRPrintModal from "./KittingQRPrintModal";
import BarcodeScannerModal from "./BarcodeScannerModal";
import FailureReasonsModal from "./FailureReasonsModal";
import FailureReasonSubmittedModal from "./FailureReasonSubmittedModal";
import SupervisorLoginModal from "./SupervisorLoginModal";
import SupervisorLoginSuccessModal from "./SupervisorLoginSuccessModal";
import StockTransferScannerModal from "./StockTransferScannerModal";
import StockTransferMaterialSelectionModal from "./StockTransferMaterialSelectionModal";
import FailureReasonMaterialSelectionModal from "./FailureReasonMaterialSelectionModal";
import NewStockTransferLabelDisplayModal from "./NewStockTransferLabelDisplayModal";
import {
  useScanning,
  useMaterialVerify,
  useOrder,
} from "../../../store/materialPlanner";

function ScanningPage({ orderId }) {
  const navigate = useNavigate();
  const [searchParams, setSearchParams] = useSearchParams();

  const [localUI, setLocalUI] = useState({
    submitLoading: false,
    submitDisabled: true,
  });
  const [isSubmitBtnEnabled, setIsSubmitBtnEnabled] = useState(true); // not using localUI state variable, I have defined new state variable
  const [isScanBarcodeEnabled, setIsScanBarcodeEnabled] = useState(true); // not using localUI state variable, I have defined new state variable
  const [isPrintHoldQtyBtnVisible, setIsPrintHoldQtyBtnVisible] =
    useState(false); // not using localUI state variable, I have defined new state variable
  const [tableData, setTableData] = useState([]);
  const {
    fetchMaterialsRequired,
    setOrderHoldQuantity,
    updateOrderState,
    generateLabels,
    submitFailureReason,
    supervisorLogin,
    stockTransfer,
    getKittingLabel,
    printLabels,
    error: orderError,
    loading: ordersLoading,
  } = useScanning();

  const {
    verifyMaterial,
    error: verifyMaterialError,
    loading: verifyMaterialLoading,
  } = useMaterialVerify();
  const [breakPacketsData, setBreakPacketsData] = useState({});
  const [loadingTableData, setLoadingTableData] = useState();
  const [total, setTotal] = useState(0);
  const [page, setPage] = useState(0);
  const [showBreakPacketModal, setShowBreakPacketModal] = useState(false);
  const [isKittingQRPrintModalOpen, setIsKittingQRPrintModalOpen] =
    useState(false);
  const [showModal, setShowModal] = useState(false);
  const [scannedData, setScannedData] = useState("");
  const [stockTransferScannedData, setStockTransferScannedData] = useState("");
  const [issuedMaterialQty, setIssuedMaterialQty] = useState({
    material_on_hold: {},
  }); // to store detailed value of issued materials; collected prop in tableData state only stores total qty for each material
  const { getOrderHoldQuantity, submitBOM } = useOrder();
  const [isFailureReasonsModalOpen, setIsFailureReasonsModalOpen] =
    useState(false);
  const [
    isFailureReasonSubmittedModalOpen,
    setIsFailureReasonSubmittedModalOpen,
  ] = useState(false);
  const [isSupervisorLoginModalOpen, setIsSupervisorLoginModalOpen] =
    useState(false);
  const [
    isSupervisorLoginSuccessModalOpen,
    setIsSupervisorLoginSuccessModalOpen,
  ] = useState(false);
  const [isSupervisorLoginDone, setIsSupervisorLoginDone] = useState(false);
  const [isStockTransferScannerModalOpen, setIsStockTransferScannerModalOpen] =
    useState(false);
  const [
    isStockTransferMaterialSelectionModalOpen,
    setIsStockTransferMaterialSelectionModalOpen,
  ] = useState(false);
  const [
    isFailureReasonMaterialSelectionModalOpen,
    setIsFailureReasonMaterialSelectionModalOpen,
  ] = useState(false);
  const [
    isNewStockTransferLabelDisplayModalOpen,
    setIsNewStockTransferLabelDisplayModalOpen,
  ] = useState(false);
  const [isStockTransferDone, setIsStockTransferDone] = useState(null);
  const [barcode, setBarcode] = useState("");
  const [partiallyFilledMaterialCodes, setPartiallyFilledMaterialCodes] =
    useState(null);
  const [
    selectedStockTransferMaterialCode,
    setSelectedStockTransferMaterialCode,
  ] = useState(null);
  const [selectedFailureReasonId, setSelectedFailureReasonId] = useState(null);
  const [
    selectedFailureReasonMaterialCode,
    setSelectedFailureReasonMaterialCode,
  ] = useState(null);

  const handleSubmit = async () => {
    const orderId = searchParams.get("orderId");
    const orderType = searchParams.get("orderType");
    try {
      // if Assembly type order, check for partial fulfilment
      if (orderType === "ASSEMBLY" && !isSupervisorLoginDone) {
        console.log("tableData", tableData);

        const allCollected = checkIfAllCollected(tableData);
        if (!allCollected) {
          setIsFailureReasonsModalOpen(true);
          return;
        }
      }
      // If execution has come to this line, it means that either order type is ACC/LPC or it a fully filled Assembly type order
      updateOrderState(orderId, "RUNNING").then((updateOrderStateData) => {
        console.log("updateOrderState data", updateOrderStateData);
        submitBOM(orderId, {
          material_issue: issuedMaterialQty.material_on_hold,
        }).then((data) => {
          console.log("submit BOM data", data);
          // If assembly type order, instead of navigating to overview page, show the print kitting QR modal
          // print kitting QR modal will navigate the user to overview page after clicking any option(yes/no)
          if (orderType === "ASSEMBLY") {
            setIsKittingQRPrintModalOpen(true);
            return;
          }
          navigate(`/material-planner/`);
        });
      });
    } catch (error) {
      console.error(error.message);
      alert(error.message);
      //TODO - Add toast message
    }
  };
  const inputRef = useRef(null);
  const handleKeyPress = (e) => {
    if (e.key === "Enter") {
      console.log("Scanned Barcode:", e.target.value);
      setBarcode(e.target.value);
      handleScan(e.target.value);
      e.target.value = ""; // Clear the input for the next scan
    }
  };

  const COLUMNS = [
    {
      id: 1,
      title: "Material",
      render: (rowData) => rowData.material_name,
    },
    {
      id: 2,
      title: "Location",
      render: (rowData) => rowData.location,
    },
    {
      id: 3,
      title: "Required Qty",
      render: (rowData) => rowData.required,
    },
    {
      id: 4,
      title: "Collected Qty",
      render: (rowData) => (
        <div>
          <span>
            {rowData.collected} {``}
          </span>
        </div>
      ),
    },
    {
      id: 5,
      title: "-",
      render: (rowData) => <StatusCell rowData={rowData} />,
    },
  ];

  const StatusCell = ({ rowData }) => {
    // switch (rowData.collectionStatus) {
    //   case COLLECTION_STATUS.DONE:
    //     return (
    //       <span className="material-planner-layout-action-icon">
    //         <CheckCircleOutlineIcon sx={{ color: "#86D56A" }} />
    //       </span>
    //     );
    //   case COLLECTION_STATUS.EXCEEDS:
    //     return (
    //       <span className="material-planner-layout-action-icon">
    //         <Button
    //           onClick={() => {
    //             setShowBreakPacketModal(true);
    //           }}
    //           white={true}
    //           width="8rem"
    //           height="2.5rem"
    //           bordered={true}
    //           customStyle={{
    //             border: "1px solid #3E3E3E",
    //             backgroundColor: "#FFFFFF",
    //           }}
    //         >
    //           Confirm
    //         </Button>
    //       </span>
    //     );
    //   default:
    //     return null;
    // }
  };

  // handling the break packet modal submit
  const handleBreakPacketActionModalSubmit = () => {
    setShowBreakPacketModal(false);
  };
  // handling the break packet modal denial
  const handleBreakPacketActionModalDenial = () => {
    setShowBreakPacketModal(false);
  };

  const checkIfAllCollected = (data) => {
    const allCollected = data.every((item) => item.required === item.collected);
    console.log("allCollected", allCollected);

    return allCollected;
  };

  const getPartiallyFilledMaterialCode = (data) => {
    const partiallyFilledMaterial = data.find(
      (item) => item.required > item.collected
    );
    return partiallyFilledMaterial
      ? partiallyFilledMaterial.material_code
      : null;
  };

  const getPartiallyFilledMaterialCodes = (data) => {
    const partiallyFilledMaterials = data.filter(
      (item) => item.required > item.collected
    );
    return partiallyFilledMaterials.length > 0
      ? partiallyFilledMaterials.map((item) => item.material_code)
      : [];
  };

  const getCurrentDate = () => {
    const today = new Date();
    const year = today.getFullYear();
    const month = String(today.getMonth() + 1).padStart(2, "0");
    const day = String(today.getDate()).padStart(2, "0");
    return `${year}-${month}-${day}`;
  };

  const handleScan = (data) => {
    setScannedData(data);
  };
  const handleStockTransferModalScan = (data) => {
    if (data) {
      setStockTransferScannedData(data);
    }
  };
  const handleBarcodeScannerModalDenial = () => {
    setShowModal(false);
  };
  const updateIssuedMaterialQty = (
    material_id,
    new_grn_number,
    new_quantity
  ) => {
    setIssuedMaterialQty((prevState) => {
      // Create a copy of the previous state
      const newState = JSON.parse(JSON.stringify(issuedMaterialQty));
      // Check if the material_id exists
      if (newState.material_on_hold[material_id]) {
        // If it exists, add the new grn_number and quantity to the array
        newState.material_on_hold[material_id] = [
          ...newState.material_on_hold[material_id],
          { grn_number: new_grn_number, quantity: new_quantity },
        ];
      } else {
        // If it doesn't exist, create a new key and add the new grn_number and quantity
        newState.material_on_hold[material_id] = [
          { grn_number: new_grn_number, quantity: new_quantity },
        ];
      }
      console.log("newState", newState);
      const orderId = searchParams.get("orderId");
      // update order hold quantity and order status with new material on hold state.
      // This is being done here to get the latest issuedMaterialQty state for setOrderHoldQuantity API call without waiting for React to update the issuedMaterialQty state
      setOrderHoldQuantity(orderId, newState).then((data) => {
        console.log("setOrderHoldQuantity data", data);

        updateOrderState(orderId, "ISSUING").then((updateOrderStateData) => {
          console.log("updateOrderState data", updateOrderStateData);
          searchParams.set("orderStatus", "ISSUING");
          setSearchParams(searchParams);
        });
      });
      return newState;
    });
    setOrderHoldQuantity(orderId, issuedMaterialQty).then((data) => {
      console.log("setOrderHoldQuantity data", data);

      updateOrderState(orderId, "ISSUING").then((updateOrderStateData) => {
        console.log("updateOrderState data", updateOrderStateData);
      });
    });
  };

  const updateTableDataWithIssuedMaterialQty = (
    collectedQtyData,
    tableData
  ) => {
    return tableData.map((item) => {
      const materialCode = item.material_code;
      let collectedQuantity = 0;
      if (collectedQtyData[materialCode]) {
        collectedQuantity = collectedQtyData[materialCode].reduce(
          (sum, entry) => sum + entry.quantity,
          0
        );
      }
      return { ...item, collected: collectedQuantity };
    });
  };

  const handleBarcodeScannerModalSubmit = (inputData) => {
    // IMPORTANT: all of this logic is valid for PLAN stage orders
    const [materialCode, quantityString, unit, grnNumber, grnDate] =
      inputData.split("#");
    const quantity = parseFloat(quantityString);

    const orderType = searchParams.get("orderType");
    const currentMaterial = tableData.find(
      (item) => item.material_code === materialCode
    );
    const qtyRequired = currentMaterial.required - currentMaterial.collected;
    console.log("currentMaterial.collected", currentMaterial.collected);
    console.log("qtyRequired", qtyRequired);

    // check if Assembly type order and large qty box scanned, then don't call material verifier API, just call label generator API
    if (orderType == "ASSEMBLY") {
      if (qtyRequired === 0) {
        alert(
          "Item already collected in full required quantity. Please scan a different item or submit the order."
        );
        return;
      }
      if (qtyRequired && quantity > qtyRequired) {
        // TODO: fix case when already scanned 1 box, and then 2nd box scanned is large (currently final collected qty exceeds required qty by already scanned 1 box amt)
        const excessQty = quantity - qtyRequired;
        generateLabels(
          materialCode,
          grnNumber,
          grnDate,
          unit,
          qtyRequired,
          excessQty
        ).then((data) => {
          // Update the collected property in tableData
          // const updatedTableData = tableData.map((item) => {
          //   if (item.material_code === materialCode) {
          //     return { ...item, collected: item.collected + qtyRequired };
          //   }
          //   return item;
          // });
          // Update the state with the new tableData
          // setTableData(updatedTableData);
          // updateIssuedMaterialQty(materialCode, grnNumber, qtyRequired);

          setShowModal(false);
          setBreakPacketsData({
            qty1: qtyRequired,
            qty2: excessQty,
            label1: data.label1,
            label2: data.label2,
          });
          setShowBreakPacketModal(true);
          // TODO - write a separate function to check if order is fulfilled (check if collected qty of all materials in the
          // order is equal to required qty. If yes, then set isSubmitBtnEnabled to true)
          // console.log("updatedTableData", updatedTableData);
        });
        return;
      }
    }

    // else, Call material verifier API
    verifyMaterial(
      searchParams.get("orderId"),
      materialCode,
      grnNumber,
      quantity
    )
      .then((data) => {
        if (data.message === "OK") {
          // TODO - if order type is assembly, then
          // if scanned qty is more than required qty, call label generator API
          // and set [materialCode, quantityString, unit, grnNumber, grnDate] to first label values
          // Also, display both the labels on screen and ask user to break packet into 2 parts
          // and paste label 1 on first packet with qty <qty-1>
          // and paste label 2 on first packet with qty <qty-2>
          // And tell user that label 1 is already scanned and collected qty updated

          if (orderType == "ASSEMBLY") {
            if (quantity <= qtyRequired) {
              // handle other case
              const updatedTableData = tableData.map((item) => {
                if (item.material_code === materialCode) {
                  return { ...item, collected: item.collected + quantity };
                }
                return item;
              });
              // Update the state with the new tableData
              // setTableData(updatedTableData);
              setTableData((tableData) => {
                // Use prevState to compute the new state
                const newState = tableData.map((item) => {
                  if (item.material_code === materialCode) {
                    return { ...item, collected: item.collected + quantity };
                  }
                  return item;
                });
                return newState;
              });

              // update issuedMaterialQty state with newly scanned material
              updateIssuedMaterialQty(materialCode, grnNumber, quantity);

              // new
              const orderId = searchParams.get("orderId");

              // setOrderHoldQuantity(orderId, issuedMaterialQty).then((data) => {
              //   console.log("setOrderHoldQuantity data", data);

              //   updateOrderState(orderId, "ISSUING").then(
              //     (updateOrderStateData) => {
              //       console.log("updateOrderState data", updateOrderStateData);
              //     }
              //   );
              // });

              console.log("updatedTableData", updatedTableData);
              setShowModal(false);
            }
          }

          if (orderType === "ACC" || orderType === "LPC") {
            // Update the collected property in tableData
            const updatedTableData = tableData.map((item) => {
              if (item.material_code === materialCode) {
                return { ...item, collected: item.collected + quantity };
              }
              return item;
            });
            // Update the state with the new tableData
            setTableData(updatedTableData);
            // update issuedMaterialQty state with newly scanned material
            updateIssuedMaterialQty(materialCode, grnNumber, quantity);

            // new
            console.log(
              "issuedMaterialQty before setOrderHoldQuantity on scan QR code",
              issuedMaterialQty
            );

            // setOrderHoldQuantity(orderId, issuedMaterialQty).then((data) => {
            //   console.log("setOrderHoldQuantity data", data);

            //   updateOrderState(orderId, "ISSUING").then(
            //     (updateOrderStateData) => {
            //       console.log("updateOrderState data", updateOrderStateData);
            //     }
            //   );
            // });
            setShowModal(false);
          }
        } else {
          alert(
            "Incorrect bar code. Please try again with different bar code."
          );
        }
      })
      .catch((error) => {
        console.error("Error during verification:", error);
      });
    // STUB bypassing verify material API for the time being to test print functionality
    setScannedData(null);
  };

  const handleFailureReasonsModalDenial = () => {
    setIsFailureReasonsModalOpen(false);
  };

  const handleFailureReasonsModalSubmit = (
    failureReasonId,
    failureReasonDescription
  ) => {
    if (
      failureReasonId &&
      failureReasonId === "34a1747e-46fe-417b-891f-e2ab52ff1352"
    ) {
      console.log("stock transfer needed");
      setIsFailureReasonsModalOpen(false);
      setStockTransferScannedData(null);
      const stockTransferMaterialCodes =
        getPartiallyFilledMaterialCodes(tableData);
      setPartiallyFilledMaterialCodes(stockTransferMaterialCodes);
      setIsStockTransferMaterialSelectionModalOpen(true);
      //TODO where is submitFailureReason being called for stock transfer case?
      return;
    }
    if (failureReasonId) {
      console.log("failureReasonId", failureReasonId);
      console.log("failureReasonDescription", failureReasonDescription);
      // select the material (show FailureReasonMaterialSelectionModal)
      const stockTransferMaterialCodes =
        getPartiallyFilledMaterialCodes(tableData);
      setPartiallyFilledMaterialCodes(stockTransferMaterialCodes);
      setSelectedFailureReasonId(failureReasonId);
      setIsFailureReasonsModalOpen(false);
      setIsFailureReasonMaterialSelectionModalOpen(true);      
    }
  };

  const handleFailureReasonSubmittedModalSubmit = () => {
    console.log("handleFailureReasonSubmittedModalSubmit");
    setIsFailureReasonSubmittedModalOpen(false);
    setIsSupervisorLoginModalOpen(true);
  };
  const handleSupervisorLoginModalDenial = () => {
    setIsSupervisorLoginModalOpen(false);
  };

  const handleSupervisorLoginModalSubmit = (username, password) => {
    console.log("username, password", username, password);
    supervisorLogin(username, password).then((data) => {
      console.log("supervisorLogin data", data);
      if (data && data.message === "Authentication successful") {
        setIsSupervisorLoginModalOpen(false);
        setIsSupervisorLoginSuccessModalOpen(true);
        setIsSupervisorLoginDone(true);
        // if assembly type order and stock transfer done, call handleBarcodeScannerModalSubmit();
        // if (isStockTransferDone) {
        //   handleBarcodeScannerModalSubmit(stockTransferScannedData);
        // }
      }
    });
  };
  const handleSupervisorLoginSuccessModalSubmit = () => {
    console.log("handleSupervisorLoginSuccessModalSubmit");
    setIsSupervisorLoginSuccessModalOpen(false);
  };
  const handleStockTransferScannerModalDenial = () => {
    console.log("handleStockTransferScannerModalDenial");
    setIsStockTransferScannerModalOpen(false);
  };
  const handleStockTransferScannerModalSubmit = () => {
    console.log("handleStockTransferScannerModalSubmit");

    if (stockTransferScannedData) {
      const [materialCode2, quantityString2, unit2, grnNumber2, grnDate2] =
        stockTransferScannedData.split("#");
      const quantity2 = parseFloat(quantityString2);
      console.log(
        "materialCode2, quantity2, unit2, grnNumber2, grnDate2",
        materialCode2,
        quantity2,
        unit2,
        grnNumber2,
        grnDate2
      );
      // call stock transfer API
      stockTransfer(
        selectedStockTransferMaterialCode,
        materialCode2,
        quantity2,
        grnNumber2,
        unit2,
        grnDate2
      ).then((data) => {
        console.log("stockTransfer data", data);
        if (data) {
          // handleScan(data.label);
          setStockTransferScannedData(data.label);
          console.log("data.label", data.label);

          //TODO submit failure reason API call with stock transfer data

          setIsStockTransferScannerModalOpen(false);
          setIsNewStockTransferLabelDisplayModalOpen(true);
        }
      });
    }
  };
  const handleStockTransferMaterialSelectionModalDenial = () => {
    console.log("handleStockTransferMaterialSelectionModalDenial");
    setIsStockTransferMaterialSelectionModalOpen(false);
  };
  const handleStockTransferMaterialSelectionModalSubmit = (materialCode) => {
    console.log("handleStockTransferMaterialSelectionModalSubmit");
    console.log("materialCode", materialCode);
    setSelectedStockTransferMaterialCode(materialCode);

    setIsStockTransferMaterialSelectionModalOpen(false);
    setIsStockTransferScannerModalOpen(true);
  };
  const handleFailureReasonMaterialSelectionModalDenial = () => {
    console.log("handleFailureReasonMaterialSelectionModalDenial");
    setIsFailureReasonMaterialSelectionModalOpen(false);
  };
  const handleFailureReasonMaterialSelectionModalSubmit = (materialCode) => {
    console.log("handleFailureReasonMaterialSelectionModalSubmit");
    console.log("materialCode", materialCode);
    setSelectedFailureReasonMaterialCode(materialCode);

    
    // call submit failure reason API with data
    //TODO move this to handleFailureReasonMaterialSelectionModalSubmit
    const orderId = searchParams.get("orderId");
    // const materialCode = getPartiallyFilledMaterialCode(tableData);
    console.log("materialCode", materialCode);
    const todaysDate = getCurrentDate();
    console.log("todaysDate", todaysDate);

    submitFailureReason(
      orderId,
      materialCode,
      selectedFailureReasonId,
      todaysDate
    ).then((data) => {
      console.log("submitFailureReason data", data);
      if (data) {
        
        setIsFailureReasonMaterialSelectionModalOpen(false);
        setIsFailureReasonSubmittedModalOpen(true);
      }
    }).catch((error) => {
      setIsFailureReasonsModalOpen(true);
    });
  };
  const handleKittingQRPrintModalDenial = () => {
    console.log("handleKittingQRPrintModalDenial");
    setIsKittingQRPrintModalOpen(false);
    navigate(`/material-planner/`);
  };
  const handleKittingQRPrintModalSubmit = () => {
    console.log("handleKittingQRPrintModalSubmit");
    // call get kitting QR label API
    const orderId = searchParams.get("orderId");
    getKittingLabel(orderId).then((kittingLabeldata) => {
      console.log("getKittingLabel data", kittingLabeldata);
      const transformedKittingLabelDataArray = [
        {
          head: "Dhoot Transmission",
          Fg_Code: kittingLabeldata.fg_code,
          Order_no: kittingLabeldata.order_no.toString(),
          Quantity: kittingLabeldata.quantity.toString(),
          QR_DATA: kittingLabeldata.label, // Mapping label to QR_DATA
        },
      ];
      console.log(
        "transformedKittingLabelDataArray",
        transformedKittingLabelDataArray
      );
      // call print API with transformedKittingLabelDataArray
      printLabels(transformedKittingLabelDataArray).then((data) => {
        console.log("printLabels data", data);
        // if success, close modal and redirect to orders page
        setIsKittingQRPrintModalOpen(false);
        navigate(`/material-planner/`);
      });
    });
  };
  const handleNewStockTransferLabelDisplayModalSubmit = () => {
    console.log("handleNewStockTransferLabelDisplayModalSubmit");
    // scan newly generated stock transfer label
    // here we are assuming that label generate is valid so we are not calling verify material API
    setIsNewStockTransferLabelDisplayModalOpen(false);
    setIsStockTransferDone(true);
    setIsSupervisorLoginModalOpen(true);
  };
  const handlePrintHoldQty = () => {
    console.log("handlePrintHoldQty");
    const table = document.createElement("table");
    const headers = ["S.no", "Material Code", "Quantity", "GRN Number"];
    const headerRow = document.createElement("tr");

    // Append header cells
    headers.forEach((header) => {
      const th = document.createElement("th");
      th.textContent = header;
      headerRow.appendChild(th);
    });
    table.appendChild(headerRow);

    // Initialize serial number
    let serialNumber = 1;

    // Append data rows
    for (const material in issuedMaterialQty.material_on_hold) {
      issuedMaterialQty.material_on_hold[material].forEach((entry) => {
        const row = document.createElement("tr");

        // Create cells for each data point
        const serialNumberCell = document.createElement("td");
        serialNumberCell.textContent = serialNumber++;
        row.appendChild(serialNumberCell);

        const materialCell = document.createElement("td");
        materialCell.textContent = material;
        row.appendChild(materialCell);

        const quantityCell = document.createElement("td");
        quantityCell.textContent = entry.quantity;
        row.appendChild(quantityCell);

        const grnNumberCell = document.createElement("td");
        grnNumberCell.textContent = entry.grn_number;
        row.appendChild(grnNumberCell);

        table.appendChild(row);
      });
    }

    const printWindow = window.open("", "_blank");
    printWindow.document.write(
      "<html><head><title>Print Table</title><style>" +
        "table { width: 100%; border-collapse: collapse; }" +
        "th, td { border: 1px solid #dddddd; text-align: center; padding: 8px; }" +
        "th { background-color: #f2f2f2; }" +
        "tr:nth-child(even) { background-color: #f9f9f9; }" +
        "tr:nth-child(odd) { background-color: #ffffff; }" +
        "</style></head><body>"
    );
    printWindow.document.write(table.outerHTML);
    printWindow.document.write("</body></html>");
    printWindow.document.close();
    printWindow.print();
  };  
  
  useEffect(() => {
    // if searchParams contain orderId, then fetch materials required for that order
    searchParams.get("orderId") &&
      fetchMaterialsRequired(searchParams.get("orderId")).then((data) => {
        if (searchParams.get("orderStatus") !== "RUNNING") {
          inputRef.current.focus(); // Automatically focus the input field when the component mounts (for running type order, barcode input field is not displayed)
        }

        // setTableData(data.items);
        // TODO update this logic so that when api sends collected property or collected property is already present, that case is handled
        // If the order status is PLAN, add "collected" prop to every material with zero initial value;
        if (searchParams.get("orderStatus") === "PLAN") {
          const updatedTableData = data.items.map((item) => ({
            ...item,
            collected: 0,
          }));
          setTableData(updatedTableData);
        }
        // If the order status is ISSUING, don't add "collected" prop; it will be supplied by backend
        if (
          searchParams.get("orderStatus") === "ISSUING" ||
          searchParams.get("orderStatus") === "RUNNING"
        ) {
          const updatedTableData = data.items;
          // call get order on hold materials qty API and update collected qty value in updatedTableData
          getOrderHoldQuantity(searchParams.get("orderId")).then((data) => {
            console.log("getOrderHoldQuantity data", data);
            setIssuedMaterialQty({ material_on_hold: data });

            const newTableData = updateTableDataWithIssuedMaterialQty(
              data,
              updatedTableData
            );
            console.log("newTableData", newTableData);
            setTableData(newTableData);
            // TODO : also make GET issued material qty for holding order and set issuedMaterialQty from the data
          });
        }
        if (searchParams.get("orderStatus") === "RUNNING") {
          setIsSubmitBtnEnabled(false);
          setIsScanBarcodeEnabled(false);
          setIsPrintHoldQtyBtnVisible(true);
        }
      });

  }, []);
  useEffect(() => {
    if (scannedData) {
      handleBarcodeScannerModalSubmit(scannedData);
    }
  }, [scannedData]);
  // useEffect(() => {
  //   if (stockTransferScannedData) {
  //     handleBarcodeScannerModalSubmit(stockTransferScannedData);
  //   }
  // }, [stockTransferScannedData]);
  return (
    <div className="mx-3 my-2 mp-instruction-layout">
      {/* Header   */}
      <div className="mp-instruction-header">
        <Breadcrumb />
        {isScanBarcodeEnabled && (
          <div
            style={{
              display: "flex",
              alignItems: "center",
              position: "fixed",
              top: "8vw", // Adjust as needed
              left: "200px", // Adjust as needed
              marginLeft: "1rem",
              width: "60vw", // Adjust as needed
              height: "4vw",
              backgroundColor: "white", // Optional: to make the background stand out
              padding: "10px", // Optional: for some padding
              // boxShadow: "0 4px 8px rgba(0, 0, 0, 0.1)", // Optional: for a subtle shadow effect
              zIndex: 1000, // Ensure it stays on top of other elements
            }}
          >
            <p className="instruction-text" style={{ marginRight: "10px" }}>
              Please Scan the Barcode or enter Qty
            </p>
            <input
              ref={inputRef}
              type="text"
              onKeyPress={handleKeyPress}
              style={{ width: "100%", padding: "10px", fontSize: "16px" }}
              placeholder="Click here and Scan or Type barcode here"
            />
          </div>
        )}
      </div>
      {/* Body   */}
      <div className="instruction-layout-body">
        <Table
          columns={COLUMNS}
          rows={tableData}
          rowsPerPage={5}
          loading={loadingTableData === "fetch"}
          titleCellClasses="c-gray fw-700"
          page={page}
          setPage={setPage}
          paginator
          serialize
          total={total}
        />
      </div>
      {/* Footer   */}
      <div className="material-planner-layout-footer">
        <Button
          onClick={handleSubmit}
          disabled={!isSubmitBtnEnabled}
          color="green"
          width="8rem"
          height="2.5rem"
          loading={localUI.submitLoading}
          customStyle={{
            backgroundColor: "#86D56A",
          }}
        >
          Submit
        </Button>
        {isPrintHoldQtyBtnVisible && (
          <Button
            onClick={handlePrintHoldQty}
            color="blue"
            width="10rem"
            height="2.5rem"
            loading={localUI.submitLoading}
          >
            Print Hold Quanity
          </Button>
        )}
      </div>

      {/* Break Packet Modal */}
      {showBreakPacketModal && (
        <Modal
          open={showBreakPacketModal}
          onClose={() => setShowBreakPacketModal(false)}
          width="75vw"
          top="5vh"
          hideCloseIcon={crossIcon}
        >
          <LabelsGeneratedModal
            qty1={breakPacketsData.qty1}
            qty2={breakPacketsData.qty2}
            label1={breakPacketsData.label1}
            label2={breakPacketsData.label2}
            noStepFn={handleBreakPacketActionModalDenial}
            nextStepFn={handleBreakPacketActionModalSubmit}
          />
        </Modal>
      )}
      {/* Break Packet Modal */}
      {isKittingQRPrintModalOpen && (
        <Modal
          open={isKittingQRPrintModalOpen}
          onClose={() => setIsKittingQRPrintModalOpen(false)}
          width="75vw"
          top="5vh"
          hideCloseIcon={crossIcon}
        >
          <KittingQRPrintModal
            orderId={searchParams.get("orderId")}
            getKittingLabel={getKittingLabel}
            noStepFn={handleKittingQRPrintModalDenial}
            nextStepFn={handleKittingQRPrintModalSubmit}
          />
        </Modal>
      )}
      {/* Failure Reasons Modal */}
      {isFailureReasonsModalOpen && (
        <Modal
          open={isFailureReasonsModalOpen}
          onClose={() => setIsFailureReasonsModalOpen(false)}
          width="50vw"
          top="10vh"
          hideCloseIcon={false}
        >
          {" "}
          <FailureReasonsModal
            noStepFn={handleFailureReasonsModalDenial}
            nextStepFn={handleFailureReasonsModalSubmit}
          />{" "}
        </Modal>
      )}
      {/* Stock Transfer Material Selection Modal */}
      {isStockTransferMaterialSelectionModalOpen && (
        <Modal
          open={isStockTransferMaterialSelectionModalOpen}
          onClose={() => setIsStockTransferMaterialSelectionModalOpen(false)}
          width="50vw"
          top="10vh"
          hideCloseIcon={false}
        >
          {" "}
          <StockTransferMaterialSelectionModal
            noStepFn={handleStockTransferMaterialSelectionModalDenial}
            nextStepFn={handleStockTransferMaterialSelectionModalSubmit}
            materialCodes={partiallyFilledMaterialCodes}
          />{" "}
        </Modal>
      )}
      {/* Failure Reason Material Selection Modal */}
      {isFailureReasonMaterialSelectionModalOpen && (
        <Modal
          open={isFailureReasonMaterialSelectionModalOpen}
          onClose={() => setIsFailureReasonMaterialSelectionModalOpen(false)}
          width="50vw"
          top="10vh"
          hideCloseIcon={false}
        >
          {" "}
          <FailureReasonMaterialSelectionModal
            noStepFn={handleFailureReasonMaterialSelectionModalDenial}
            nextStepFn={handleFailureReasonMaterialSelectionModalSubmit}
            materialCodes={partiallyFilledMaterialCodes}
          />{" "}
        </Modal>
      )}
      {/* Failure Reason Submitted Modal */}
      {isFailureReasonSubmittedModalOpen && (
        <Modal
          open={isFailureReasonSubmittedModalOpen}
          onClose={() => setIsFailureReasonSubmittedModalOpen(false)}
          width="50vw"
          top="10vh"
          hideCloseIcon={false}
        >
          {" "}
          <FailureReasonSubmittedModal
            nextStepFn={handleFailureReasonSubmittedModalSubmit}
          />{" "}
        </Modal>
      )}
      {/* Supervisor Login Modal */}
      {isSupervisorLoginModalOpen && (
        <Modal
          open={isSupervisorLoginModalOpen}
          onClose={() => setIsSupervisorLoginModalOpen(false)}
          width="50vw"
          top="10vh"
          hideCloseIcon={false}
        >
          {" "}
          <SupervisorLoginModal
            noStepFn={handleSupervisorLoginModalDenial}
            nextStepFn={handleSupervisorLoginModalSubmit}
          />{" "}
        </Modal>
      )}
      {/* Supervisor Login Success Modal */}
      {isSupervisorLoginSuccessModalOpen && (
        <Modal
          open={isSupervisorLoginSuccessModalOpen}
          onClose={() => setIsSupervisorLoginSuccessModalOpen(false)}
          width="50vw"
          top="10vh"
          hideCloseIcon={false}
        >
          {" "}
          <SupervisorLoginSuccessModal
            nextStepFn={handleSupervisorLoginSuccessModalSubmit}
          />{" "}
        </Modal>
      )}
      {/* Stock Transfer Scanner Modal */}
      {isStockTransferScannerModalOpen && (
        <Modal
          open={isStockTransferScannerModalOpen}
          onClose={() => setIsStockTransferScannerModalOpen(false)}
          width="50vw"
          top="10vh"
          hideCloseIcon={false}
        >
          {" "}
          <StockTransferScannerModal
            onScan={handleStockTransferModalScan}
            noStepFn={handleStockTransferScannerModalDenial}
            nextStepFn={handleStockTransferScannerModalSubmit}
          />{" "}
        </Modal>
      )}
      {/* New Stock Transfer Label Display Modal */}
      {isNewStockTransferLabelDisplayModalOpen && (
        <Modal
          open={isNewStockTransferLabelDisplayModalOpen}
          onClose={() => setIsNewStockTransferLabelDisplayModalOpen(false)}
          width="50vw"
          top="10vh"
          hideCloseIcon={false}
        >
          {" "}
          <NewStockTransferLabelDisplayModal
            nextStepFn={handleNewStockTransferLabelDisplayModalSubmit}
            newLabel={stockTransferScannedData}
          />{" "}
        </Modal>
      )}
    </div>
  );
}
export default ScanningPage;
